@import '../mixins.scss';
@import "../FontFace.scss";

$sub_menu_left: 500px;

* {
  margin: 0;
  padding: 0;
}

body {
  background-image: url("../../assets/img/background_2.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
  margin: 0 10px;
}

#view {
  position: relative;
  height: calc(100vh - 40px);
  width: calc(100vw - 32px);

  margin: 20px 5px auto;

  background: #c4c4c4;
  opacity: 0.8;
  overflow: auto;

  .settings {
    .btn {
      @include btn();

    }
  }

  .Admin {
    height: calc(100vh - 150px);
    display: flex;
    justify-content: center;
    text-align: center;

    div {

      display: flex;
      flex-direction: column;
      justify-content: center;

      .input {
        @include answer_group();
        margin-left: 3px;
        margin-top: 3px;
        margin-bottom: 3px;
      }


      .btn {
        @include btn();
        margin: 5px auto;
      }

    }
  }



  #menu,
  .menu2 {
    width: 98%;
    border: 2px solid rgb(50, 50, 50);
    margin: 15px auto;
    justify-content: space-between;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    background: #c4c4c4;
    opacity: 0.8;

    #control {
      padding: 0 15px;

      #lecturer,
      #log_out {
        margin-left: 10px;
      }

      .btn {
        @include btn();
      }
    }

    .name {
      padding-left: 15px;
      @include font(36px);
    }

    .input {
      @include answer_group();
      margin-left: 3px;
      margin-top: 3px;
      margin-bottom: 3px;
    }
  }

  .ResultViewDatatable,
  .ListViewDatatable {
    &>div:first-child {
      display: block;
      height: 100%;
    }


    width: 98%;
    margin: 0 auto;
    border: 2px solid black;
    background: #c4c4c4;
    height: calc(100% - 160px);

    .rdt_Table {
      background-color: #c4c4c4;

      .rdt_TableBody {
        overflow-y: auto;

        .rdt_TableRow {
          background: #c4c4c4;

          &:hover {
            background: gray;
            color: white;


            .rdt_TableCell {
              .btn {
                background-color: #c4c4c4;
              }
            }
          }
        }

        .rdt_TableCell {
          .action {
            display: flex;
            justify-content: space-between;
            width: 220px;

            .btn {
              @include btn();
              margin: 0;
              width: 100px;

            }
          }
        }
      }
    }



    .form-check-label {
      font-size: 22px;
      margin-left: -3px;
    }
  }

  .menu2 {
    display: flex;
    justify-content: space-between;

    div:first-child {
      margin-left: 15px;

      .btn {
        @include btn();
        margin-left: 10px;
      }
    }

    input {
      margin-right: 20px;
    }
  }
}

.swal2-container {
  .swal2-modal {
    .swal2-actions {
      button {
        @include btn();
        padding: 0;
      }
    }

    .swal2-content {
      #swal2-content {
        .input {
          @include answer_group();
        }

      }
    }
  }
}

@import "../medias.scss";