@import "../mixins.scss";

.Lecturer {
  display: flex;
  justify-content: center;
  align-items: center;


  .Lecturer_SelectType {
    display: flex;
    flex-flow: column;
    justify-content: center;
    height: 120px;
    margin: auto 0;
    @include font(20px);

    .RBText {
      margin-left: 15px;
    }
  }

  .Lecturer_SelectView_Group {
    @include group();

    margin-left: 22px;

    .Question {
      @include question();
    }

    .Lecturer_SelectView_Group_InputGroup {
      display: flex;
      flex-direction: column;
      //align-items: center;
      width: 97%;

      .AnswersGroup {
        @include answer_group();
        width: 95%;
      }

      .Lecturer_SelectView_Group_InputGroup--sub:last-child {
        //margin-left: 10px;

        div {
          display: flex;
          flex-direction: row;
        }

        .AnswersGroup {
          width: 180px;
        }

        .none {
          margin-top: 12px;

          &:hover {
            border: none;
          }
        }

        div:last-child {
          display: flex;
          flex-direction: column;
          //margin-left: -50px;
        }
      }

      .Lecturer_SelectView_Group_InputGroup--sub {
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 100%;

        .attr {
          @include attr();

          margin-top: 10px;

          .checkbox {
            height: 32px;
          }

          .radio {
            height: 32px;
          }
        }

        &:last-child {
          justify-content: left;
        }
      }
    }

    button {
      @include button();
      @include font(1.2em);
    }
  }

  .Sortable {
    .Lecturer_SelectView_Group_InputGroup {
      .Lecturer_SelectView_Group_InputGroup--sub:last-child {
        margin-right: 0;
      }
    }
  }

  .TrueFalse {
    .Lecturer_SelectView_Group_InputGroup {
      .Lecturer_SelectView_Group_InputGroup--sub:last-child {
        margin-right: 0;
      }

      .Lecturer_SelectView_Group_InputGroup--sub {
        display: flex;
        flex-direction: row;

        .form-check {
          padding-left: 30px;
          padding-right: 33px;
          @include font(1.2em);
        }
      }
    }
  }

  .Single {
    .Lecturer_SelectView_Group_InputGroup {
      .Lecturer_SelectView_Group_InputGroup--sub:last-child {
        margin-right: 33px;
      }
    }
  }
}