@import "../mixins.scss";

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 100vh;



    .Main {


        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        box-shadow: 8px 8px 20px 10px rgba(0, 0, 0, 0.4),
            0px 0px 20px 10px rgba(0, 0, 0, 0.4);

        height: 45vh;
        width: 500px;

        backdrop-filter: blur(5px);
        border: 2px solid gray;

        .input {
            @include answer_group();
            width: 85%;
            //margin-top: 10px;

            &:hover {
                border: 2px solid black;
            }


        }

        .btn {
            @include btn();
            background-color: #c4c4c4;
            margin: 0 auto;
            margin-top: 10px;
        }

        .forgot {
            @include font(1.2em);
            display: flex;
            justify-content: flex-end;
            width: 400px;
            margin-top: 7px;

            &:hover {
                cursor: pointer;
            }
        }

        .error {
            @include font(1.2em);
            color: white;
            justify-content: center;
            text-align: center;
            width: 400px;
            margin-top: 5px;
        }

        .show {
            display: flex
        }


        .container--send {
            display: flex;
            flex-direction: row;

            .btn {
                width: 80px;
                margin-left: 10px;
            }

            .input {
                width: 330px
            }
        }

        .container--code_input {
            .code_input {
                @include answer_group();
                width: 15px;
                padding-right: 10px;


                margin-left: 10px;

                &:first-child {
                    margin-left: 0px;
                }
            }

            display: flex;
            flex-direction: row;


            .btn {
                margin-left: 10px;
            }
        }




    }
}